/* fonts */
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap');


body {
  background: linear-gradient(to right, #e3a4f0, #87b7ee);
  font-family: montserrat;
  text-align: center;
}

.note-bg {
  background-image: url(./bg-image.png);
  height: 80vh;
  width: 90vw;
  margin: 0 auto;
  border-radius: 20px;
  max-width: 45em;
}

.date {
  font-family: dancing script;
  font-style: normal;
  font-size: 2.5rem;
  padding-top: 1rem;

}

.title {
  font-size: 2.5rem;
}

.add-tasks {
  display: flex;
  flex-wrap: wrap;
}

.input-box {
  width: 70vw;
  max-width: 35rem;
  height: 2rem;
  font-size: 1.5rem;
  margin: 0 auto;
  border-radius: 10px;
}

.time-add {
  margin: 0 auto;
  display: flex;
  gap: 2rem;
  padding: 1rem;
}

.add-btn {
  width: 5rem;
  height: 2rem;
  border-radius: 10px;
}

.add-btn:hover {
  background-color: #474747;
  color: #ffffff;
}

.added-list li {
  list-style: none;
  font-size: 1rem;
  text-align: start;
  padding-bottom: 0.5rem;
}

.checkbox {
  margin-right: 0.5rem;
}

.more-options button {
  background-color: #3d8ce6;
  color: #ffffff;
  width: 6.5rem;
  height: 2rem;
  border-radius: 10px;
  left: 33%;
  bottom: 1rem;
  margin: 1rem;
}